/* SmartWizard Theme: Dots */

.sw-theme-dots {
    .sw-container {
        min-height: 300px;
    }

    .step-content {
        padding: 15px;
        border: none;
        background-color: $white;
        text-align: start;
    }

    .sw-toolbar {
        background: $white;
        border-radius: 0 !important;
        padding-inline: 10px;
        margin-block-end: 0 !important;
    }

    .sw-toolbar-top {
        border-block-end-color: #ddd !important;
    }

    .sw-toolbar-bottom {
        border-block-start-color: #ddd !important;
        border-block-end-color: #ddd !important;
    }

    >ul.step-anchor {
        position: relative;
        background: $white;
        border: 0px solid #5c6287 !important;
        list-style: none;

        &:before {
            content: " ";
            position: absolute;
            inset-inline-start: 20px;
            inset-block-start: 59px;
            inset-block-end: 0;
            width: 96%;
            height: 5px;
            background-color: #f5f5f5;
            border-radius: 3px;
            z-order: 0;
            z-index: 95;
        }

        >li {
            border: none;

            >a {
                position: relative;
                text-align: center;
                font-weight: bold;
                background: transparent;
                border: none;
                color: #5c6287;
                text-decoration: none;
                outline-style: none;
                z-index: 96;
                display: block;
            }
        }
    }
}

/* Anchors styles */

.step-anchor .nav-tabs .nav-item .nav-link {
    color: #5c6287 !important;
}

.sw-theme-dots {
    >ul.step-anchor>li>a:before {
        content: ' ';
        position: absolute;
        inset-block-end: 14px;
        inset-inline-start: 36%;
        margin-block-start: 20px;
        display: block;
        border-radius: 50%;
        color: #428bca;
        background: $background;
        border: none;
        width: 30px;
        height: 30px;
        text-decoration: none;
        z-index: 98;
    }

    nav-tabs .nav-link:hover:not(.disabled) {
        color: inherit;
        background: transparent !important;
    }

    >ul.step-anchor>li {
        >a {
            &:after {
                content: ' ';
                position: relative;
                inset-inline-start: 35%;
                inset-block-end: 5px;
                margin-block-start: 24px;
                display: block;
                width: 16px;
                height: 16px;
                background: #d9d9d9;
                border-radius: 50%;
                z-index: 99;
            }

            &:hover {
                color: #5c6287;
                background: transparent;
            }

            &:focus {
                color: #5c6287;
                border: none;
            }
        }

        &.clickable>a:hover {
            color: #999;
        }

        &.active>a:hover {
            border: none;
        }

        &.done>a {
            color: #5c6287;

            // &:after {
            //     background: #caeaff;
            // }
        }

        &.danger>a {
            color: #d5cef3;

            &:after {
                background: #d5cef3;
            }
        }

        &.disabled>a {
            color: #eee !important;

            &:hover {
                color: #eee !important;
            }

            &:after {
                background: #eee;
            }
        }
    }
}

