      animation-delay: -200ms;
    }

    &:nth-child(2) {
      -webkit-animation-delay: -100ms;
      -moz-animation-delay: -100ms;
      -o-animation-delay: -100ms;
      animation-delay: -100ms;
    }

    &:nth-child(3) {
      -webkit-animation-delay: 0ms;
      -moz-animation-delay: 0ms;
      -o-animation-delay: 0ms;
      animation-delay: 0ms;
    }
  }

  width: 54px;
  height: 18px;

  &.la-sm {
    width: 26px;
    height: 8px;

    > div {
      width: 4px;
      height: 4px;
      margin: 2px;
    }
  }

  &.la-2x {
    width: 108px;
    height: 36px;

    > div {
      width: 20px;
      height: 20px;
      margin: 8px;
    }
  }

  &.la-3x {
    width: 162px;
    height: 54px;

    > div {
      width: 30px;
      height: 30px;
      margin: 12px;
    }
  }
}

